.product_card{
	background-color: #fff;
	border-radius: 5px;
	text-align: center;
	padding-bottom: 45px;
	margin-bottom: 30px;
	height: 380px;
}
.product_card__img{
	display: flex;
	align-items: center;
	justify-content: center;
	height: 250px;
	padding-bottom: 10px;
}
.product_card__img img{
	max-width: 100%;
	height: auto;
}
.product_card__content{
	padding: 0 20px 0 20px;
}
.product_card__title{
	color: @product-title-clr;
	margin-bottom: 20px;
	font-size: 16px;
}
.product_card__color{
	margin-bottom: 15px;
	color: @product-color-clr;
	font-size: 12px;  
	text-transform: uppercase;
	letter-spacing: 1.2px;
}
.product_card__price{
	font-size: 14px;
}