.header-cart{
   font-size: 11px;
   width: 90px;
   display: flex;
   align-items: center;
   margin-right: 30px;

}
.header-cart__icon{
   margin-right: 8px;
   
}
.header-cart__title{
   margin-right: 5px;
   color: @dark-blue;

}
.header-cart__counter{
   width: 24px;
   height: 24px;
   border-radius: 12px;
   background-color: #e3e8f0;
   justify-content: center;
   align-items: center;
   display: flex;
}
