*:focus {
	outline: none;
}
.header-bg{
	background-color: #fff;
	border-bottom: 1px solid @divider;
}

.header{
	padding-top: 30px;
	padding-bottom: 30px;
	display: flex;
	justify-content: space-between;
	align-items: center;

}

.header-right{
	color: @dark-blue;
   text-transform: uppercase;
   letter-spacing: 0.55px;
	display: flex;
	font-size: 11px;
	align-items: center;
}
