.navigation-bg{
   background-color: #fff;
}
.header-navigation{
   display: flex;
   justify-content: space-between;
   align-items: center;
   height: 56px;
   color: @gray-font;
   font-size: 11px;
   text-transform: uppercase;
   letter-spacing: 1.1px;
   margin-bottom: 30px;
}
.navigation-right{
   display: flex;
   height: 100%;
}
.header-support{
   display: flex;
   align-items: center;
   margin-right: 32px;
      &:hover{
      color: @dark-blue;
      transition: .5s;
   }
}
.header-search{
   text-transform: none;
   letter-spacing: normal;
   padding: 0 21px 0 37px;
   border-right: 1px solid @divider;
   border-left: 1px solid @divider;
   display: flex;
   align-items: center;
}
.header-search__label{
   position: relative;
   padding-left: 26px;
   &::before{
      content: "";
      background-image: url(../../img/icons/Search_Icon.svg);
      background-repeat: no-repeat;
      background-position: center;
      width: 13px;
      display: block;
      height: 13px;
      position: absolute;
      top: 3px;
      left: 0;
   }
}
.header-search__input{
   color: #a1a8bd;
   border: none;
   font-size: 14px;
   font-weight: 700;
   &::placeholder{
      color: #a1a8bd;
   }
}