.product-card-big{
	height: 380px;
	border-radius: 5px;
	background-color: #fff;
}
.product-card-big__img {
	height: 305px;
	margin-bottom: 15px;
	position: relative;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}
.product-card-big__price{
	width: 62px;
	height: 24px;
	border-radius: 4px;
	background-color: #1c1e23;
	color: #fff;
	top: 12px;
	left: 12px;
	position: absolute;
	text-align: center;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: 1.2px;
	line-height: 24px;
}
.product-card-big__content{
	display: flex;
	justify-content: space-between;
	padding: 0 20px 0 20px;
	align-items: center;
}
.product-card-big__title{
	color: @product-title-clr;
	font-size: 16px;
	margin-bottom: 7px;
}
.product-card-big__color{
	color: @product-color-clr;
	font-size: 12px;
	letter-spacing: 1.2px;
	text-transform: uppercase;
}