.button {
	height: 32px;
	border-radius: 16px;
	border: 2px solid @br-color;
	justify-content: center;
	align-items: center;
	display: flex;
	text-decoration: none;
	color: @dark-blue;
	padding: 12px 18px;
	letter-spacing: 1.1px;
	transition: all 0.2s ease-in;
	&:hover {
		background-color: @br-color;
	}
}
.button-buy {
	
	display: flex;
}
.specs{
	border-radius: 17px 0 0 17px;
	border: 2px solid #f0f2f7;
	border-right: none;
	font-size: 11px;
	padding: 7px 40px;
	text-transform: uppercase;
}
button {
	color: @dark-blue;
	font-weight: 700;
	font-size: 11px;
	padding: 7px 30px;
	border: 2px solid #f0f2f7;
	border-radius: 0 17px 17px 0;
	left: -5px;
	text-transform: uppercase;
	letter-spacing: 1.1px;
	transition: all 0.2s ease-in;
	
	&:hover {
		background-color: #f0f2f7;
	}
}
