.navigation-list{
   display: flex;

}
.navigation-list__item{
   text-transform: uppercase;
   text-decoration: none;
   color: @gray-font;
   margin-right: 30px;

   &:last-child{
      margin: 0;
   }
   &:hover{
      color: @dark-blue;
      transition: .5s;
   }
}
.navigation-list__item.activ{
   color: @dark-blue;
}